import React, { FC, useEffect, useRef, useState } from 'react';
import {
  generateViewport,
  Viewport,
  ViewportContext,
} from './utilities/ViewportContext';
import { useComponentBox } from './hooks';
import { Footer, NavBar } from './components/generic';
import { PageContainer } from './components/layout';
import { Box, Flex } from '@chakra-ui/core';

const App: FC = ({ children }) => {
  const [viewport, setViewport] = useState<Viewport>(generateViewport(window));

  useEffect(() => {
    const handleViewportChange = () => setViewport(generateViewport(window));
    window.addEventListener('resize', handleViewportChange);
    return () => window.removeEventListener('resize', handleViewportChange);
  }, []);

  const navBarBoxRef = useRef(null);
  const navBarBox = useComponentBox(navBarBoxRef);

  return (
    <ViewportContext.Provider value={viewport}>
      <Box
        ref={navBarBoxRef}
        position={'fixed'}
        w={'100%'}
        zIndex={999}
        top={0}
        left={0}
        bg={'white'}
      >
        <NavBar />
      </Box>

      <Flex
        mt={`${navBarBox?.height || 75}px`}
        direction={'column'}
        minHeight={'100vh'}
      >
        <PageContainer>{children}</PageContainer>
        <Box mt={'auto'}>
          <Footer />
        </Box>
      </Flex>
    </ViewportContext.Provider>
  );
};

export default App;
